html {
  --scrollbarBG: #CFD8DC;
  --thumbBG: #90A4AE;
  scroll-behavior: smooth;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: rgb(250, 248, 244);
}
.container{
  padding: 0;

}
.main__flex__container{
  height: 100vh;
}
.flex__main__components{
  height: 90vh;
  overflow-y: scroll;
  overflow-x: hidden;
}
::-webkit-scrollbar {
  width: 5px;
}
.cursor-pointer{
  cursor: pointer;
}
.login{
    max-width:450px;
}
.login__main{
    display: grid;
    place-items: center;
    height: 100vh;
    background: url(/static/media/fete.4f9bde97.jpg);
    background-size: cover;
}
.notification{
    width:300px;
    min-height: 60px;
    color:white;
    position: fixed;
    top:50px;
    z-index:100;
  }
  
.splash__main{
    height: 100vh;
    background: url(/static/media/fete.4f9bde97.jpg);
    background-size: cover;
}
.splash__main img{
    max-height: 30vh;
    max-width: 450px;
}
.text-purple{
    color: rgb(107, 3, 21);
    font-weight: 800;
}

.fete__photo{
 width:100%;
 height: auto;
}
